.statistics {
  min-width: 320px;
  max-width: 420px;
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  padding: 10px;
  box-shadow: 0 0 4px 1px #2f2f2f, inset 0 0 10px 1px #d9d5d5;
  border-radius: 4px;
}

.statList {
  display: flex;
}

.label,
.percentage {
  display: block;
}

.statList li:not(:last-child) {
  border-right: 1px solid #9f9595;
}

.statList li {
  width: calc(100% / 5);
}

.label {
  margin-bottom: 4px;
  font-size: 14px;
  color: #9f9595;
}
